import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import { TRAIN_TYPE, FACULTY, DORMITORY_ROOM, DORMITORY_BUILDING } from 'routes/data/category';
import { InfoCard } from 'components/common';
import routeConstant from 'routes/route/routeConstant';
import {
  getQuickLinkTrainTypeNo,
  getQuickLinkFacultyNo,
  getQuickLinkCourseNo,
  getQuickLinkStudyFieldNo,
  getQuickLinkStudyFieldChildNo,
  getQuickLinkDormNo,
  getQuickLinkDormRoomNo,
  getQuickLinkComeFromNo,
} from 'services/quicklink';
import ComeFromIcon from 'components/icons/ComeFromIcon';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    ...TRAIN_TYPE,
    service: getQuickLinkTrainTypeNo,
  },
  {
    ...FACULTY,
    service: getQuickLinkFacultyNo,
  },
  {
    title: 'Khóa đào tạo',
    service: getQuickLinkCourseNo,
    url: '/courses/list',
  },
  {
    title: 'Ngành đào tạo',
    service: getQuickLinkStudyFieldNo,
    url: '/specaialize/list',
  },
  {
    title: 'CN đào tạo',
    service: getQuickLinkStudyFieldChildNo,
    url: '/specializeTraining/list',
  },
  {
    ...DORMITORY_BUILDING,
    service: getQuickLinkDormNo,
  },
  {
    ...DORMITORY_ROOM,
    service: getQuickLinkDormRoomNo,
  },
  {
    title: 'Xuất thân',
    service: getQuickLinkComeFromNo,
    url: routeConstant.DANH_MUC__TUYEN_SINH__XUAT_THAN,
    icon: ComeFromIcon,
  },
];

class DashboardCategory extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardCategory {...props} />
  </Page>
);



